const handleImageUploadError = (error) => {
  console.error('Image upload error:', error);
  alert(`Lỗi upload ảnh: ${error}`);
};

const formatTimeFromHourMinute = (hour, minute) => {
  if (!hour && !minute) return '';
  const h = hour ? String(hour).padStart(2, '0') : '00';
  const m = minute ? String(minute).padStart(2, '0') : '00';
  return `${h}:${m}`;
};

const parseTimeToHourMinute = (timeString) => {
  if (!timeString) return { hour: '', minute: '' };
  const [hour, minute] = timeString.split(':');
  return {
    hour: hour ? hour : '',
    minute: minute ? minute : ''
  };
};

  // Swipe functionality
  const touchStartX = useRef(null);
  const touchStartY = useRef(null);
  const containerRef = useRef(null);

  useEffect(() => {
    loadShipmentDetail();
    loadMasterData();
    loadShipmentImages();
  }, [shipmentId]);

  // Load shipment images
  const loadShipmentImages = async () => {
    try {
      const result = await imageService.getShipmentImages(shipmentId);
      if (result.success && result.data) {
        setImages(result.data);
      }
    } catch (err) {
      console.error('Error loading shipment images:', err);
    }
  };

  // Delete image from server
  const handleDeleteImage = async (imageId, filePath) => {
    if (!filePath) {
      alert('Không thể xóa ảnh: Đường dẫn không hợp lệ');
      return;
    }

    const confirmDelete = window.confirm('Bạn có chắc chắn muốn xóa ảnh này không?');
    if (!confirmDelete) return;

    setDeletingImageId(imageId);

    try {
      const result = await imageService.deleteImage(filePath);
      
      if (result.success) {
        // Remove from local state
        setImages(prev => prev.filter(img => img.id !== imageId));
        alert('Xóa ảnh thành công');
        
        // Reload images to ensure we have the latest data
        loadShipmentImages();
      } else {
        alert(result.message || 'Không thể xóa ảnh');
      }
    } catch (error) {
      alert(`Lỗi khi xóa ảnh: ${error.message}`);
    } finally {
      setDeletingImageId(null);
    }
  };

  // Swipe functionality
  const handleTouchStart = (e) => {
    touchStartX.current = e.touches[0].clientX;
    touchStartY.current = e.touches[0].clientY;
  };



  const handleTouchEnd = (e) => {
    if (!touchStartX.current || !touchStartY.current) return;

    const touchEndX = e.changedTouches[0].clientX;
    const touchEndY = e.changedTouches[0].clientY;
    const deltaX = touchEndX - touchStartX.current;
    const deltaY = touchEndY - touchStartY.current;

    // Check if it's a horizontal swipe (more horizontal than vertical)
    if (Math.abs(deltaX) > Math.abs(deltaY) && Math.abs(deltaX) > 50) {
      // Swipe right to go back
      if (deltaX > 0) {
        onBack();
      }
    }

    touchStartX.current = null;
    touchStartY.current = null;
  };

  // Phone number handling
  const extractPhoneNumbers = (contactText) => {
    if (!contactText) return [];
    
    // Regular expression to match Vietnamese phone numbers
    const phoneRegex = /(?:\+84|84|0)(?:3[2-9]|5[689]|7[06-9]|8[1-689]|9[0-46-9])\d{7}/g;
    const matches = contactText.match(phoneRegex) || [];
    
    // Also look for any sequence of 10-11 digits
    const digitRegex = /\b\d{10,11}\b/g;
    const digitMatches = contactText.match(digitRegex) || [];
    
    // Combine and deduplicate
    const allNumbers = [...new Set([...matches, ...digitMatches])];
    return allNumbers;
  };

  const makePhoneCall = (phoneNumber) => {
    // Clean the phone number
    const cleanNumber = phoneNumber.replace(/\D/g, '');
    window.location.href = `tel:${cleanNumber}`;
  };

  const renderContactWithPhones = (contactText) => {
    if (!contactText) return 'N/A';
    
    const phoneNumbers = extractPhoneNumbers(contactText);
    
    if (phoneNumbers.length === 0) {
      return <span className="font-medium">{contactText}</span>;
    }

    if (phoneNumbers.length === 1) {
      return (
        <div className="flex items-center gap-2">
          <span className="font-medium flex-1">{contactText}</span>
          <button
            onClick={() => makePhoneCall(phoneNumbers[0])}
            className="bg-green-500 text-white px-3 py-1 rounded-lg hover:bg-green-600 flex items-center gap-1 text-sm"
            title="Gọi điện"
          >
            <Phone className="w-4 h-4" />
            Gọi
          </button>
        </div>
      );
    }

    // Multiple phone numbers
    return (
      <div className="space-y-2">
        <span className="font-medium block">{contactText}</span>
        <div className="flex flex-wrap gap-2">
          {phoneNumbers.map((phone, index) => (
            <button
              key={index}
              onClick={() => makePhoneCall(phone)}
              className="bg-green-500 text-white px-3 py-1 rounded-lg hover:bg-green-600 flex items-center gap-1 text-sm"
              title={`Gọi ${phone}`}
            >
              <Phone className="w-4 h-4" />
              {phone}
            </button>
          ))}
        </div>
      </div>
    );
  };

  const toggleSection = (section) => {
    setCollapsedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };

  const copyToClipboard = async (text) => {
    try {
      await navigator.clipboard.writeText(text);
      alert('Đã sao chép địa chỉ');
    } catch (err) {
      // Fallback for older browsers
      const textArea = document.createElement('textarea');
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      alert('Đã sao chép địa chỉ');
    }
  };

  const openInGoogleMaps = (address) => {
    console.log('addess' , address);
    const url = address;
    window.open(url, '_blank');
  };

  
const loadShipmentDetail = async () => {
  try {
    const result = await shipmentService.getShipmentDetail(shipmentId);
    if (result.success && result.data && result.data.length > 0) {
      const detail = result.data[0];
      setShipmentDetail(detail);
      
      // Prepare edited shipment with delivery points structure for dispatcher
      const editedData = {
        ...detail,
        deliveryPoints: []
      };
      
      // Convert delivery data to delivery points array for editing
      for (let i = 1; i <= 4; i++) {
        const address = detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_ADDRESS_${i}`]];
        if (address) {
          editedData.deliveryPoints.push({
            idKhachHangGiao: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_CUSTOMER_ID_${i}`]] || '',
            idDiaChiGiaoHang: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_ADDRESS_ID_${i}`]] || '',
            diaChiGiaoHang: address,
            lienhegiao: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_CONTACT_${i}`]] || '',
            linkmapgiao: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_MAP_LINK_${i}`]] || '',
            ngaygiao: formatDateFromAPIToInput(detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_DATE_${i}`]]) || '', // FIX: Convert date format
            giogiao: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_HOUR_${i}`]] || '',
            phutgiao: detail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_MINUTE_${i}`]] || ''
          });
        }
      }
      
      // Ensure at least one delivery point for editing
      if (editedData.deliveryPoints.length === 0) {
        editedData.deliveryPoints.push({
          idKhachHangGiao: '',
          idDiaChiGiaoHang: '',
          diaChiGiaoHang: '',
          lienhegiao: '',
          linkmapgiao: '',
          ngaygiao: '',
          giogiao: '',
          phutgiao: ''
        });
      }
      
      // FIX: Convert date formats for input fields
      editedData[SHIPMENT_DETAIL_FIELDS.INPUT_DATE] = formatDateFromAPIToInput(detail[SHIPMENT_DETAIL_FIELDS.INPUT_DATE]);
      editedData[SHIPMENT_DETAIL_FIELDS.PICKUP_DATE] = formatDateFromAPIToInput(detail[SHIPMENT_DETAIL_FIELDS.PICKUP_DATE]);
      
      setEditedShipment(editedData);
    } else {
      setError('Không thể tải thông tin chi tiết chuyến xe');
    }
  } catch (err) {
    setError('Có lỗi xảy ra khi tải dữ liệu');
  }
};

const handlePickupTimeChange = (timeString) => {
  const { hour, minute } = parseTimeToHourMinute(timeString);
  setEditedShipment(prev => ({
    ...prev,
    [SHIPMENT_DETAIL_FIELDS.PICKUP_HOUR_1]: hour,
    [SHIPMENT_DETAIL_FIELDS.PICKUP_MINUTE_1]: minute
  }));
};

// Handle delivery time change
const handleDeliveryTimeChange = (index, timeString) => {
  const { hour, minute } = parseTimeToHourMinute(timeString);
  setEditedShipment(prev => ({
    ...prev,
    deliveryPoints: prev.deliveryPoints.map((point, i) => 
      i === index ? { ...point, giogiao: hour, phutgiao: minute } : point
    )
  }));
};


  const loadMasterData = async () => {
    try {
      const [customersResult, driversResult, trucksResult, payloadsResult,
        shippingAddressesResult
      ] = await Promise.all([
        masterDataService.getCustomers(),
        masterDataService.getDrivers(),
        masterDataService.getTrucks(),
        masterDataService.getPayload(),
        masterDataService.getShippingAddresses()
      ]);

      if (customersResult.success) setCustomers(customersResult.data || []);
      if (driversResult.success) setDrivers(driversResult.data || []);
      if (trucksResult.success) setTrucks(trucksResult.data || []);
      if (payloadsResult.success) setPayloads(payloadsResult.data || []);
      if (shippingAddressesResult.success) setShippingAddresses(shippingAddressesResult.data || []);
    } catch (err) {
      console.error('Error loading master data:', err);
    } finally {
      setLoading(false);
    }
  };

  const getCustomerById = (customerId) => {
    const customer = customers.find(c => c[CUSTOMER_FIELDS.ID] === customerId);
    return customer ? customer[CUSTOMER_FIELDS.NAME] : '';
  };

  const getDriverById = (driverId) => {
    const driver = drivers.find(d => d[DRIVER_FIELDS.ID] === driverId);
    return driver || null;
  };

  const getTruckById = (truckId) => {
    const truck = trucks.find(t => t[TRUCK_FIELDS.ID] === truckId);
    return truck || null;
  };

  const getPayloadById = (payloadId) => {
    const payload = payloads.find(p => p[PAYLOAD_FIELDS.ID] === payloadId);
    return payload ? payload[PAYLOAD_FIELDS.WEIGHT] : '';
  };


  const getNextAvailableStatuses = (currentStatus) => {
    const statusFlow = [
      { value: SHIPMENT_STATUS.WAITING_CONFIRMATION, text: SHIPMENT_STATUS_TEXT[0], color: 'bg-blue-500' },
      { value: SHIPMENT_STATUS.ACCEPTED, text: SHIPMENT_STATUS_TEXT[1], color: 'bg-green-500' },
      { value: SHIPMENT_STATUS.IN_DELIVERY, text: SHIPMENT_STATUS_TEXT[2], color: 'bg-yellow-500' },
      { value: SHIPMENT_STATUS.DELIVERED, text: SHIPMENT_STATUS_TEXT[4], color: 'bg-gray-500' },
      { value: SHIPMENT_STATUS.COMPLETED, text: SHIPMENT_STATUS_TEXT[5], color: 'bg-gray-300' },
      { value: SHIPMENT_STATUS.CANCELLED, text: SHIPMENT_STATUS_TEXT[9], color: 'bg-red-500' }
    ];

    const currentIndex = statusFlow.findIndex(s => s.value === currentStatus);
    if (currentIndex === -1 || currentStatus === SHIPMENT_STATUS.COMPLETED) return [];
    
    // Only allow next status in sequence, plus cancel option (except for completed)
    const nextStatuses = [];
    if (currentIndex < statusFlow.length - 2) { // Not completed yet
      nextStatuses.push(statusFlow[currentIndex + 1]);
      if (currentStatus !== SHIPMENT_STATUS.CANCELLED) {
        nextStatuses.push(statusFlow[statusFlow.length - 1]); // Cancel option
      }
    }
    
    return nextStatuses;
  };

  const handleStatusUpdate = async (newStatus) => {
    if (updating) return;

    // Check if images are required for specific status transitions
    const isTransitionFromAcceptedToInDelivery = 
      currentStatus === SHIPMENT_STATUS.ACCEPTED && newStatus === SHIPMENT_STATUS.IN_DELIVERY;
    
    const isTransitionFromInDeliveryToDelivered = 
      currentStatus === SHIPMENT_STATUS.IN_DELIVERY && newStatus === SHIPMENT_STATUS.DELIVERED;
    
    // Images are required for these specific transitions
    const isImageRequired = isTransitionFromAcceptedToInDelivery || isTransitionFromInDeliveryToDelivered;
    
    // If images are required, check if any images exist for this shipment
    if (isImageRequired && images.length === 0) {
      // Show image upload section
      setShowImageUpload(true);
      
      // Show a modal dialog for image upload
      const confirmUpload = window.confirm('Bắt buộc phải upload ảnh khi chuyển trạng thái này. Bạn có muốn upload ảnh ngay bây giờ không?');
      
      if (confirmUpload) {
        // Scroll to the image upload section
        const imageUploadSection = document.querySelector('.image-upload-section');
        if (imageUploadSection) {
          imageUploadSection.scrollIntoView({ behavior: 'smooth' });
        }
      }
      
      return;
    }

    setUpdating(true);
    try {
      // Pass userName from userData to the API call
      const result = await shipmentService.updateStatus(
        shipmentId, 
        newStatus, 
        userData?.UserName || userData?.userName || ''
      );
      
      if (result.success) {
        setShipmentDetail(prev => ({
          ...prev,
          [SHIPMENT_DETAIL_FIELDS.STATUS]: newStatus
        }));
        alert('Cập nhật trạng thái thành công');
      } else {
        alert(result.message || 'Cập nhật trạng thái thất bại');
      }
    } catch (error) {
      alert('Có lỗi xảy ra khi cập nhật trạng thái');
    } finally {
      setUpdating(false);
    }
  };

  const handleEditToggle = () => {
    if (isEditing) {
      // Cancel editing
      setEditedShipment(shipmentDetail);
      setIsEditing(false);
    } else {
      // Start editing
      setIsEditing(true);
      // Expand sections for editing
      if (userData?.type === USER_TYPES.DISPATCHER) {
        setCollapsedSections({
          basicInfo: false,
          vehicleInfo: false,
          notes: false
        });
      }
    }
  };

  const handleFieldChange = (field, value) => {
    setEditedShipment(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleDeliveryPointChange = (index, field, value) => {
    setEditedShipment(prev => ({
      ...prev,
      deliveryPoints: prev.deliveryPoints.map((point, i) => 
        i === index ? { ...point, [field]: value } : point
      )
    }));
  };

  const addDeliveryPoint = () => {
    if (editedShipment.deliveryPoints.length < 4) {
      setEditedShipment(prev => ({
        ...prev,
        deliveryPoints: [
          ...prev.deliveryPoints,
          {
            idKhachHangGiao: '',
            idDiaChiGiaoHang: '',
            diaChiGiaoHang: '',
            lienhegiao: '',
            linkmapgiao: '',
            ngaygiao: '',
            giogiao: '',
            phutgiao: ''
          }
        ]
      }));
    }
  };

  const removeDeliveryPoint = (index) => {
    if (editedShipment.deliveryPoints.length > 1) {
      setEditedShipment(prev => ({
        ...prev,
        deliveryPoints: prev.deliveryPoints.filter((_, i) => i !== index)
      }));
    }
  };

  const handleCustomerChange = (field, customerId, isPickup = false) => {
    const customer = customers.find(c => c[CUSTOMER_FIELDS.ID] === parseInt(customerId));
    
    if (isPickup) {
      handleFieldChange(SHIPMENT_DETAIL_FIELDS.PICKUP_CUSTOMER_ID, customerId);
      if (customer) {
        handleFieldChange(SHIPMENT_DETAIL_FIELDS.PICKUP_ADDRESS, customer[CUSTOMER_FIELDS.ADDRESS]);
        handleFieldChange(SHIPMENT_DETAIL_FIELDS.PICKUP_CONTACT, customer[CUSTOMER_FIELDS.CONTACT]);
      }
    } else {
      // Handle delivery customer change
      const [pointField, pointIndex] = field.split('_');
      const index = parseInt(pointIndex);
      
      handleDeliveryPointChange(index, 'idKhachHangGiao', customerId);
      if (customer) {
        handleDeliveryPointChange(index, 'diaChiGiaoHang', customer[CUSTOMER_FIELDS.DELIVERY_ADDRESS] || customer[CUSTOMER_FIELDS.ADDRESS]);
        handleDeliveryPointChange(index, 'lienhegiao', customer[CUSTOMER_FIELDS.CONTACT]);
      }
    }
  };



  
  const handleSaveEdit = async () => {
  if (saving) return;

  setSaving(true);
  try {
    // Prepare data for API
    const saveData = {
      idChuyenXe: shipmentId,
      ngaynhap: formatDateFromInputToAPI(editedShipment[SHIPMENT_DETAIL_FIELDS.INPUT_DATE]), // FIX: Convert format
      ngaynhan: formatDateFromInputToAPI(editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_DATE]), // FIX: Convert format
      loaivantai: editedShipment[SHIPMENT_DETAIL_FIELDS.TRANSPORT_TYPE],
      idTrongTai: editedShipment[SHIPMENT_DETAIL_FIELDS.PAYLOAD_ID],
      idXe: editedShipment[SHIPMENT_DETAIL_FIELDS.TRUCK_ID],
      idLaiXe: editedShipment[SHIPMENT_DETAIL_FIELDS.DRIVER_ID],
      
      // Required field - keep main customer
      idKhachHang: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_CUSTOMER_ID],
      idKhachHangNhan: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_CUSTOMER_ID],
      
      idDiaChiLayHang: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_ADDRESS_ID],
      diaChiLayHang: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_ADDRESS],
      lienhenhan: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_CONTACT],
      linkmapnhan: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_MAP_LINK],
      gionhan1: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_HOUR_1],
      phutnhan1: editedShipment[SHIPMENT_DETAIL_FIELDS.PICKUP_MINUTE_1],
      giatri: editedShipment[SHIPMENT_DETAIL_FIELDS.VALUE],
      ghichu: editedShipment[SHIPMENT_DETAIL_FIELDS.NOTES],
      trangthai: editedShipment[SHIPMENT_DETAIL_FIELDS.STATUS],
      userName: userData.UserName || userData.userName || ''
    };

    // Add delivery points
    editedShipment.deliveryPoints.forEach((point, index) => {
      const pointNum = index + 1;
      saveData[`ngaygiao${pointNum}`] = formatDateFromInputToAPI(point.ngaygiao); // FIX: Convert format
      saveData[`idKhachHangGiao${pointNum}`] = point.idKhachHangGiao;
      saveData[`idDiaChiGiaoHang${pointNum}`] = point.idDiaChiGiaoHang;
      saveData[`diaChiGiaoHang${pointNum}`] = point.diaChiGiaoHang;
      saveData[`lienhegiao${pointNum}`] = point.lienhegiao;
      saveData[`linkmapgiao${pointNum}`] = point.linkmapgiao;
      saveData[`giogiao${pointNum}`] = point.giogiao;
      saveData[`phutgiao${pointNum}`] = point.phutgiao;
    });

    const result = await shipmentService.saveShipment(saveData);
    if (result.success) {
      setIsEditing(false);
      alert('Cập nhật thông tin chuyến xe thành công');
      // Reload detail to get fresh data
      loadShipmentDetail();
    } else {
      alert(result.message || 'Cập nhật thông tin thất bại');
    }
  } catch (error) {
    console.error('Error saving shipment:', error);
    alert('Có lỗi xảy ra khi cập nhật thông tin');
  } finally {
    setSaving(false);
  }
};


  const formatTime = (hour, minute) => {
    if (!hour && !minute) return '';
    return `${String(hour || 0).padStart(2, '0')}:${String(minute || 0).padStart(2, '0')}`;
  };


  const getDeliveryAddresses = () => {
    if (!shipmentDetail) return [];
    
    const addresses = [];
    for (let i = 1; i <= 4; i++) {
      const address = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_ADDRESS_${i}`]];
      const contact = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_CONTACT_${i}`]];
      const date = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_DATE_${i}`]];
      const hour = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_HOUR_${i}`]];
      const minute = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_MINUTE_${i}`]];
      const linkmap = shipmentDetail[SHIPMENT_DETAIL_FIELDS[`DELIVERY_MAP_LINK_${i}`]];
      
      if (address) {
        addresses.push({
          address,
          contact,
          date,
          time: formatTime(hour, minute),
          index: i,
          linkmap
        });
      }
    }
    return addresses;
  };

