import React from 'react';
import { Calendar, FileText, LogOut } from 'lucide-react';
import { SCREENS, USER_TYPES } from '../constants';

const BottomNavigation = ({ activeScreen, onNavigate, onLogout, userData }) => {
  const navigationItems = userData.type === USER_TYPES.DISPATCHER 
  ? [
    {
      id: SCREENS.REPORT,
      icon: FileText,
      label: 'Báo cáo',
      onClick: () => onNavigate(SCREENS.REPORT)
    },
    {
      id: 'logout',
      icon: LogOut,
      label: 'Đăng xuất',
      onClick: onLogout
    }
  ] : [
    // Navigation for DRIVER (updated)
    {
      id: SCREENS.CALENDAR,
      icon: Calendar,
      label: 'Lịch',
      onClick: () => onNavigate(SCREENS.CALENDAR)
    },
    {
      id: SCREENS.REPORT,
      icon: FileText,
      label: 'Báo cáo',
      onClick: () => onNavigate(SCREENS.REPORT)
    },
    {
      id: 'logout',
      icon: LogOut,
      label: 'Đăng xuất',
      onClick: onLogout
    }
  ];
  
  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 safe-area-bottom">
      <div className="flex justify-around items-center py-2">
        {navigationItems.map((item) => {
          const Icon = item.icon;
          const isActive = activeScreen === item.id;
          
          return (
            <button
              key={item.id}
              onClick={item.onClick}
              className={`flex flex-col items-center p-2 min-w-0 flex-1 ${
                isActive ? 'text-blue-600' : 'text-gray-600'
              } hover:text-blue-600 transition-colors`}
            >
              <Icon className="w-6 h-6" />
              <span className="text-xs mt-1 truncate">{item.label}</span>
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default BottomNavigation;