export const imageService = {
  /**
   * Upload image to server
   * @param {string} userName - User name
   * @param {string} imageBase64 - Base64 encoded image
   * @param {string} fileName - File name
   * @param {string} imageType - Image type (default: 'shipment')
   * @param {string} idChuyenXe - Shipment ID
   * @param {number} trangthai - Status code (optional)
   * @returns {Promise} Upload response
   */
  uploadImage: async (userName, imageBase64, fileName, imageType = 'shipment', idChuyenXe, trangthai = null) => {
    try {
      const params = {
        keyPass: API_CONFIG.KEY_PASS,
        userName: userName,
        imageBase64: imageBase64,
        fileName: fileName,
        imageType: imageType,
        idChuyenXe: idChuyenXe,
      };
      
      // Add trangthai parameter if provided
      if (trangthai !== null) {
        params.trangthai = trangthai;
      }
      
      const response = await post(API_ENDPOINTS.UPLOAD_IMAGE, params);
      
      return parseApiResponse(response);
    } catch (error) {
      console.error('Image upload error:', error);
      return {
        success: false,
        message: error.message || 'Upload failed',
        data: null
      };
    }
  },

  /**
   * Delete image from server
   * @param {string} filePath - Path to the image file on server
   * @returns {Promise} Delete response
   */
  deleteImage: async (filePath) => {
    try {
      const response = await post(API_ENDPOINTS.DELETE_IMAGE, {
        keyPass: API_CONFIG.KEY_PASS,
        filePath: filePath
      });
      
      return parseApiResponse(response);
    } catch (error) {
      console.error('Image delete error:', error);
      return {
        success: false,
        message: error.message || 'Failed to delete image',
        data: null
      };
    }
  },

  /**
   * Get images for a shipment
   * @param {number} idChuyenXe - Shipment ID
   * @returns {Promise} Images response
   */
  getShipmentImages: async (idChuyenXe) => {
    try {
      const response = await post(API_ENDPOINTS.GET_SHIPMENT_IMAGES, {
        keyPass: API_CONFIG.KEY_PASS,
        idChuyenXe: idChuyenXe
      });
      
      return parseApiResponse(response);
    } catch (error) {
      console.error('Get shipment images error:', error);
      return {
        success: false,
        message: error.message || 'Failed to get shipment images',
        data: null
      };
    }
  },
  

  /**
   * Convert file to base64
   * @param {File} file - File object
   * @returns {Promise<string>} Base64 string
   */
  fileToBase64: (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        // Remove data:image/jpeg;base64, prefix
        const base64 = reader.result.split(',')[1];
        resolve(base64);
      };
      reader.onerror = error => reject(error);
    });
  },

  /**
   * Resize image before upload
   * @param {File} file - Image file
   * @param {number} maxWidth - Maximum width
   * @param {number} maxHeight - Maximum height
   * @param {number} quality - Image quality (0-1)
   * @returns {Promise<string>} Resized base64 string
   */
  resizeImage: (file, maxWidth = 1024, maxHeight = 768, quality = 0.8) => {
    return new Promise((resolve, reject) => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const img = new Image();

      img.onload = () => {
        try {
          // Calculate new dimensions
          let { width, height } = img;
          
          if (width > height) {
            if (width > maxWidth) {
              height = height * (maxWidth / width);
              width = maxWidth;
            }
          } else {
            if (height > maxHeight) {
              width = width * (maxHeight / height);
              height = maxHeight;
            }
          }

          canvas.width = width;
          canvas.height = height;

          // Draw and compress
          ctx.drawImage(img, 0, 0, width, height);
          const base64 = canvas.toDataURL('image/jpeg', quality).split(',')[1];
          resolve(base64);
        } catch (error) {
          reject(error);
        }
      };

      img.onerror = () => reject(new Error('Failed to load image'));
      
      // Create object URL for the image
      img.src = URL.createObjectURL(file);
    });
  },

  /**
   * Validate image file before upload
   * @param {File} file - Image file
   * @returns {object} Validation result
   */
  validateImageFile: (file) => {
    const maxSize = 5 * 1024 * 1024; // 5MB
    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/webp'];

    if (file.size > maxSize) {
      return {
        isValid: false,
        error: 'File quá lớn. Kích thước tối đa là 5MB'
      };
    }

    if (!allowedTypes.includes(file.type)) {
      return {
        isValid: false,
        error: 'Định dạng file không được hỗ trợ. Chỉ chấp nhận JPG, PNG, WebP'
      };
    }

    return { isValid: true };
  }
};

