export const notificationService = {
  /**
   * Register FCM token with the server
   * @param {string} idLaiXe - Driver ID
   * @param {string} idMay - FCM token
   * @returns {Promise} Registration response
   */
  registerFCMToken: async (idLaiXe, idMay) => {
    try {
      const response = await post(API_ENDPOINTS.SAVE_DEVICE, {
        keyPass: API_CONFIG.KEY_PASS,
        idLaiXe: idLaiXe,
        idMay: idMay
      });
      
      return parseApiResponse(response);
    } catch (error) {
      console.error('FCM token registration error:', error);
      return {
        success: false,
        message: error.message || 'Failed to register FCM token',
        data: null
      };
    }
  }
};

