// Helper function to get delivery times array
const getDeliveryTimes = (shipment) => {
  const times = [];
  for (let i = 1; i <= 4; i++) {
    const hour = shipment[`giogiao${i}`];
    const minute = shipment[`phutgiao${i}`];
    const time = formatTime(hour, minute);
    if (time) {
      times.push(time);
    }
  }
  return times;
};

