const getAddressesDisplay = (shipment) => {
  const addresses = [];
  
  // Địa chỉ lấy hàng
  if (shipment.diachinhan && shipment.diachinhan.trim()) {
    addresses.push(`Lấy: ${shipment.diachinhan.trim()}`);
  } else if (shipment.khachhangnhan && shipment.khachhangnhan.trim()) {
    // Nếu không có địa chỉ cụ thể, hiển thị tên khách hàng nhận
    addresses.push(`Lấy: ${shipment.khachhangnhan.trim()}`);
  }
  
  // Các địa chỉ giao hàng
  for (let i = 1; i <= 4; i++) {
    const address = shipment[`diachigiao${i}`];
    const customer = shipment[`khachhang${i}`];
    
    if (address && address.trim()) {
      addresses.push(`Giao ${i}: ${address.trim()}`);
    } else if (customer && customer.trim()) {
      // Nếu không có địa chỉ cụ thể, hiển thị tên khách hàng
      addresses.push(`Giao ${i}: ${customer.trim()}`);
    }
  }
  
  return addresses.length > 0 ? addresses : ['Chưa có địa chỉ'];
};

// Helper function to get customers array
const getCustomersArray = (shipment) => {
  const customers = [];
  if (shipment.khachhang && shipment.khachhang.trim()) {
    customers.push(shipment.khachhang.trim());
  }
  for (let i = 1; i <= 4; i++) {
    const customer = shipment[`khachhang${i}`];
    if (customer && customer.trim()) {
      customers.push(customer.trim());
    }
  }
  return customers;
};

// Helper function to get delivery addresses array
const getDeliveryAddressesArray = (shipment) => {
  const addresses = [];
  if (shipment.diachinhan && shipment.diachinhan.trim()) {
    addresses.push({ type: 'pickup', address: shipment.diachinhan.trim() });
  }
  for (let i = 1; i <= 4; i++) {
    const address = shipment[`diachigiao${i}`];
    if (address && address.trim()) {
      addresses.push({ type: 'delivery', address: address.trim() });
    }
  }
  return addresses;
};

// Helper function to calculate event height based on content
const calculateEventHeight = (shipment) => {
  const baseHeight = 60; // Base height for basic info
  const timeHeight = 20; // Height for time display
  const locationHeight = 40; // Height for location info (can be 2 lines)
  
  const displayInfo = getShipmentDisplayInfo(shipment);
  const pickupTime = formatTime(shipment.gionhan1, shipment.phutnhan1);
  const deliveryTime = formatTime(shipment.giogiao1, shipment.phutgiao1);
  
  let totalHeight = baseHeight;
  
  // Add height for time if present
  if (pickupTime || deliveryTime) {
    totalHeight += timeHeight;
  }
  
  // Add height for location info
  totalHeight += locationHeight;
  
  return totalHeight;
};

// Helper function to calculate day height based on events
const calculateDayHeight = (dayShipments) => {
  if (dayShipments.length === 0) {
    return 150; // Minimum height for empty day
  }
  
  const headerHeight = 60; // Height of day header
  const padding = 16; // Padding inside day container
  const eventSpacing = 8; // Space between events
  
  const totalEventHeight = dayShipments.reduce((total, shipment) => {
    return total + calculateEventHeight(shipment);
  }, 0);
  
  const totalSpacing = (dayShipments.length - 1) * eventSpacing;
  
  return headerHeight + padding + totalEventHeight + totalSpacing;
};

