import React, { useState } from 'react';
import { Truck } from 'lucide-react';
import { authService } from '../services/apiService';
import { userStorage } from '../utils/storageUtils';
import axios from 'axios';

const Login = ({ onLogin }) => {
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const v2 = async()=>{

const url = 'https://td.giangdc.company/Admin/GDCService.asmx/CheckLogin';
const data = new URLSearchParams();
data.append('keyPass', 'Gdc825@');
data.append('userName', 'hoangson');
data.append('passWord', '123');

const res =await axios.post(url, data.toString(), {
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  },
});
    console.log(res.data);
      const xmlString = res.data;

     let jsonString = '';
  const startIndex = xmlString.indexOf('<string xmlns="http://tempuri.org/">');
  const endIndex = xmlString.indexOf('</string>');

  if (startIndex !== -1 && endIndex !== -1) {
    // Extract the content between the <string> tags
    jsonString = xmlString.substring(startIndex + '<string xmlns="http://tempuri.org/">'.length, endIndex);
  } else {
    throw new Error('Could not find the expected <string> tag in the XML response.');
  }
      const parsedData = JSON.parse(jsonString);
      console.log(parsedData);
  };
  

  const handleLogin = async () => {
    if (!userName || !password) {
      setError('Vui lòng nhập đầy đủ thông tin');
      return;
    }

    setLoading(true);
    setError('');

    v2();

    try {
      const result = await authService.login(userName, password);
      
      if (result.success && result.data && result.data.length > 0) {
        const userData = result.data[0];
        userStorage.save(userData);
        onLogin(userData);
      } else {
        setError(result.message || 'Đăng nhập thất bại');
      }
    } catch (err) {
      setError('Có lỗi xảy ra. Vui lòng thử lại');
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleLogin();
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg shadow-xl p-8 w-full max-w-md">
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-20 h-20 bg-white rounded-full mb-4 shadow-lg border">
            <img src="https://blackmatrix2007.github.io/driver-pwa-app/td.png" alt="TD Logo" className="w-16 h-16 object-contain" />
          </div>
          <h1 className="text-2xl font-bold text-gray-900">Đăng Nhập</h1>
          <p className="text-gray-600 mt-2">Hệ thống quản lý vận chuyển</p>
        </div>

        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Tên đăng nhập
            </label>
            <input
              type="text"
              value={userName}
              onChange={(e) => setUserName(e.target.value)}
              onKeyPress={handleKeyPress}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Nhập tên đăng nhập"
              disabled={loading}
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Mật khẩu
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              onKeyPress={handleKeyPress}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Nhập mật khẩu"
              disabled={loading}
            />
          </div>

          {error && (
            <div className="bg-red-50 text-red-600 p-3 rounded-lg text-sm">
              {error}
            </div>
          )}

          <button
            onClick={handleLogin}
            disabled={loading}
            className="w-full bg-blue-600 text-white py-3 rounded-lg font-medium hover:bg-blue-700 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
          >
            {loading ? 'Đang đăng nhập...' : 'Đăng nhập'}
          </button>
        </div>


      </div>
    </div>
  );
};

export default Login;
